@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
    width: 100%;
    display: inline-block;
}

* {
    font-family: 'IBM Plex Sans', sans-serif;
    margin: 0;
    padding: 0;
    font-weight: 500;
}

html {
    font-size: 1vw;
}


.App{text-align:center;width:100vw;display:inline-block}

.column{display:flex;flex-direction:column}.row{display:flex;flex-direction:row}#main{width:100%;height:100vh;display:flex;flex-direction:row}#main>div{flex:1 1;height:100%;display:flex;justify-content:flex-start;align-items:flex-start;flex-direction:column}#main>div img{height:100%;width:80%;object-fit:cover}#main #main-left{display:flex;justify-content:center;align-items:flex-start}#main #main-left .uni-info{font-size:1.1rem}#main #main-left>div:first-child{display:inline-block;margin-left:50px;width:calc(100% - 2 * 50px)}#main #main-left>div:first-child>#uni-desc{font-size:2.8rem;white-space:pre-line;line-height:3rem;text-align:justify}#main #main-left>#refs{display:flex;flex-direction:row;width:calc(100% - 2 * 50px);margin-left:50px;position:relative;top:5%}#main #main-left>#refs .uni-bottom{font-size:2.2rem}#main #main-left>#refs>p{position:relative}#main #main-left>#refs>p:nth-child(2){left:calc(calc(100% - 2 * 50px) / 2);transform:translateX(-100%)}header{position:absolute;top:0;left:0;display:inline-flex;width:95%;flex-direction:row;justify-content:center;padding:30px 50px 0}header .header-settings,header #header-logo,header #header-search,header #header-buttons{display:flex;flex-direction:row;justify-content:flex-start;align-items:center}header #header-logo{flex:4 1}header #header-search{font-size:1.7rem;flex:5 1}header #header-buttons{flex:3 1;justify-content:space-between}header #header-buttons .langs{width:20%;display:flex;justify-content:space-between;font-size:1.1rem}header #header-buttons h1{font-size:1.7rem}.float-left{float:left}.float-left:after{content:"";padding-right:50px}.inactive{opacity:.4}

.column{display:flex;flex-direction:column}.row{display:flex;flex-direction:row}#about-content{display:inline-block}#about-content>div{margin-left:50px}#about-content>div>div:first-child{width:calc(100% - 2 * 50px - 415px)}#about-content>div p{text-align:left}#about-content #about-us-info{margin-top:8%;margin-bottom:6%;display:inline-flex;flex-direction:row;align-items:center}#about-content #about-us-info p:first-child{font-size:2.8rem;margin-bottom:2%}#about-content #about-us-info p.text{font-size:2.2rem}#about-content #about-us-info #about-flash{position:relative;right:261px}#about-content #about-us-info #about-logo{animation:spin 60s linear infinite}@keyframes spin{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}

.column{display:flex;flex-direction:column}.row{display:flex;flex-direction:row}.about-list{width:100%;display:inline-flex;flex-direction:row;margin-bottom:15vh}.about-list .about-content,.about-list .about-li{flex:1 1}.about-list .about-titles{display:flex;flex-direction:column;justify-content:stretch}.about-list .about-content>div{height:100%;width:85%}.about-list .about-elements{font-size:36px;text-align:left;margin-left:8.33333vw}.about-list .about-elements .about-title{margin:0.5rem 0;font-size:1.8rem}.about-list .about-elements .about-title.underline{text-decoration:underline}.about-list .about-elements .about-title.line:before{content:"-";display:inline-block}.about-list li{list-style-type:none;display:block;margin:0.8rem 0;max-width:60%;font-size:1.8rem}.about-list li:before{content:"-";display:inline-block}.text{text-align:left;margin-left:50px;font-size:2.6rem}

.partners-grid{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr)}.div1{grid-area:1 / 1 / 3 / 2}.div2{grid-area:1 / 2 / 2 / 3}.div3{grid-area:2 / 2 / 3 / 3;box-sizing:border-box;text-align:left}.div3 .header{display:flex;flex-direction:row;margin-top:20px}.div3 .header>*{margin-left:20px}.div3 .header p{font-size:2.2rem}.div3 .header h1{font-weight:500}.div3>p{padding:15px;font-size:1rem}

.column{display:flex;flex-direction:column}.row{display:flex;flex-direction:row}.values-grid{display:flex;flex-direction:row;justify-content:center;align-items:stretch;grid-gap:52px;gap:52px;position:relative}.values-grid #values-circle{position:absolute;top:0;left:0;transform-origin:0 0;animation:spin-top-left 60s linear infinite}@keyframes spin-top-left{0%{transform:rotate(0) translateX(-50%) translateY(-50%)}100%{transform:rotate(360deg) translateX(-50%) translateY(-50%)}}.values-grid>*{flex:1 1}.values-grid .content{min-height:280px;width:40px}.values-grid .content .values-number{font-size:4rem;line-height:3rem}.values-grid .content .values-content{font-size:3rem;line-height:2.25rem;margin-bottom:20px}.values-grid .content p{font-size:1rem}

.column{display:flex;flex-direction:column}.row{display:flex;flex-direction:row}.enter-container{text-align:left}.enter-container h3{font-size:2rem}.enter-container .enter-stages{grid-gap:32px;gap:32px;margin-top:7px}.enter-container p{margin-top:32px;font-size:1.5rem}#enter-after{font-size:1.3rem;width:78%;text-align:justify}

.column{display:flex;flex-direction:column}.row{display:flex;flex-direction:row}.contacts-container{min-height:80vh;width:100%}.contacts-right{margin-top:26px;width:33.33333vw;text-align:left}.contacts-right p.social{font-size:2rem;margin-bottom:22px}.contacts-right #itmo{margin:60px 0}.contacts-right .out>div{margin-bottom:10px}.contacts-right .out a{margin-left:10px}.contacts-right .devs{margin-top:80px}.contacts-right .devs a{color:black}.contacts-left{text-align:left;margin-top:26px;margin-left:calc((1 / 12) * 100vw + 50px);width:58.33333vw}.contacts-left p{display:inline-block;width:50vw;font-size:1.8rem}.contacts-left .inactive{margin-top:26px}.contacts-left .place{margin-top:104px}

